@import "../../Styles/Includes.scss";

.Info {
  &__Inner {
    padding: 4rem 2rem;
    display: flex;
    background-color: $pink;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 4rem;
    background-image: url("../../Assets/Images/blob.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;

    @include media(m) {
      background-size: contain;
      padding: 10rem 2rem;
    }
  }

  &__Grid {
    text-align: center;
    max-width: 640px;
  }

  &__Title {
    @extend %title;
  }

  &__Text {
    font-family: $body;
    line-height: 1.6;
    font-size: 1.8rem;
    color: $off-black;

    ul {
      margin-top: 1.8rem;
    }

    p {
      &:not(:first-of-type) {
        margin-top: 1.8rem;
      }

      &:not(:last-of-type) {
        margin-bottom: 1.8rem;
      }
    }
  }

  &__LinkList {
    font-family: $body;
    line-height: 1.6;
    font-size: 1.8rem;
    color: $off-black;
  }

  &__Link {
    color: $off-black;
    text-decoration: none;

    span {
      position: relative;
      display: inline-block;
      z-index: 0;

      &:before {
        content: "";
        background-color: $pink;
        width: 0;
        height: 100%;
        display: block;
        position: absolute;
        z-index: -1;
        border-radius: 0.4rem;
        transition: width 300ms ease-in-out;
      }

      &::after {
        content: "";
        background-image: url("../../Assets/Images/icon-link.svg");
        background-repeat: no-repeat;
        background-size: contain;
        margin-left: 1rem;
        width: 1rem;
        height: 1rem;
        display: inline-block;
      }
    }

    &:hover {
      span {
        &:before {
          width: 100%;
        }
      }
    }
  }

  &__RVSP {
    margin-top: 6rem;
    font-family: $bold;
    font-size: 2.4rem;
    text-align: center;
    color: $off-black;
    text-decoration: none;

    a {
      color: inherit;
      text-decoration: none;
    }
  }
}
